.counterContainer {
  position: relative;
  text-align: center;
}
.counterContainer img {
  max-height: 11.714em;
  position: relative;
  left: 0;
}
.counterContainer .count {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 10em;
  letter-spacing: 0.2px;
  font-family: Merriweather Sans;
  font-weight: 700;
  color: #0dad46;
  padding-left: 0.912em;
  line-height: normal;
}
.counterContainer .count.cw {
  color: #ffffff;
  position: relative;
  padding: 0px;
}
@media (max-width: 1023px) {
  .counterContainer {
    width: 100%;
    padding-top: 0;
  }
  .counterContainer .count {
    font-size: 2.5em;
    letter-spacing: 0.2px;
    line-height: 1.2em;
    padding-top: 0.36em;
  }
  .counterContainer .countertitle {
    font-size: 1.3em;
    letter-spacing: 0.25px;
    line-height: 1.538em;
  }
}
@media (max-width: 834px) {
  .counterContainer {
    justify-content: center;
    align-items: center;
  }
  .counterContainer .count {
    left: 0;
    right: 0;
    font-size: 6em;
    padding-top: 0;
  }
  .counterContainer img {
    max-height: 5.07em;
    margin-top: 1em;
    margin-left: -12em;
  }
}
